import React, { useState, createContext, useEffect, useCallback } from 'react';
import { ConConfig } from '../../Models/Config';
import { semaforo } from '../../Utiles/Semaforo';
export interface DolarType {
    tasa: number,
}
export const DolarContext = createContext<DolarType>({ tasa: 1 })
export default function DolarProvider({ children }: { children: any }) {
    const [tasa, setDolar] = useState(1);
    const s = semaforo();
    const update = useCallback(() => {
        if (s.isTurn('Dolares')) {
            ConConfig('').forceGetWeb('/api/Dolares/valor', dolar => {
                if (tasa !== dolar) setDolar(dolar);
                s.next();
            });
        }
    }, [tasa])
    let interval: any;
    useEffect(() => {
        interval = setInterval(update, 100)
        return () => clearInterval(interval)
    }, [tasa])
    useEffect(() => {
        s.add('Dolares');
        return () => s.clean('Dolares');
    }, [])
    return <DolarContext.Provider value={{ tasa }}>
        {children}
    </DolarContext.Provider>
}
export const SeccionConsumer = DolarContext.Consumer;